@import '_vars';
.ProductsContainer{
    font-family: var(--main-font);
    max-width: 1280px;
     margin: 2rem auto;
}
.category-title{
    margin: 0 0.5rem;
    background-color: var(--slack-blue);
    text-align: center;
    padding: var(--sm);
    border-radius: 25px;
    color: white;//var(--slack-purple);
}
.see-all a{
    padding: 0 var(--sm);
    margin: 0 0.5rem;
    text-align: right;
    text-decoration: none;
    color: black;
}
.see-all{
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

@media(min-width:300px){
    .category-title{
        font-size: large;
    }
    .see-all{
        font-size: small;
    }
 }

 @media(min-width:800px){
    .category-title{
        font-size: xx-large;
    }
 }

