.CartEmpty{
    height: 65vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 3rem;
    text-align: center;
}
.oops{
    font-weight: bold;
    margin-bottom: 3rem; 
}
.quotes{
    margin-bottom: 1rem;
    font-style: italic;
}
.CartEmpty a{
    font-size: medium;
}
.Cart{
    margin: auto 2rem;
    font-family: var(--main-font);
}
.Cart .cart-title{
    text-align: center;
    margin: 2rem 0;
    font-weight: bold;
}
.btns-cont{
    display: flex;
    justify-content: center;
    margin-top: 4rem;
}
.Cart .cart-btn{
    padding: 1rem;
    border-radius: 25px;
    margin: 0 1.8rem;
    font-weight: bold;
    cursor: pointer;
}
.btn-shop,
.btn-clear{
    background-color: var(--slack-purple);
    border: 1px solid var(--slack-purple);
    color:var(--white);
    margin-bottom: 0.6rem;
    text-decoration: none;
}
.btn-shop:hover,
.btn-clear:hover{
    background-color: var(--white);
    color: var(--slack-purple);
}

.btn-complete{
    background-color: var(--slack-green);
    border: 1px solid var(--slack-green);
    color:var(--white);
    margin-bottom: 0.6rem;
}
.btn-complete:hover{
    background-color: var(--white);
    color: var(--slack-green);
}
.total-cont{
    margin-top: 2rem;
    margin-left: auto;
    margin-right: auto;
    max-width: 100.0rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.total-cont div{
    max-width: fit-content;
}


@media(min-width:300px){
    .Cart .btns-cont button,
    .Cart .btns-cont a,
    .total-cont div p{
        font-size: x-small;
    }
    .total-cont{
        padding-right: 4rem;
    }
}
@media(min-width:800px){
    .Cart .btns-cont button,
    .Cart .btns-cont a,
    .total-cont div p{
        font-size: medium;
    }
    .total-cont{
        padding-right: 0;
    }
 }
